.card {
    background: #242422;
    margin: 10px;
    border-radius: 7px;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .2), 0 1px 5px 0 rgba(0, 0, 0, .12);
            box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .2), 0 1px 5px 0 rgba(0, 0, 0, .12);
    overflow: hidden;
    width: calc(100% - 20px);
    color: #fff;
}

.card .card-header {
    font-size: 1.6rem;
    font-weight: 700;
    color: #747474;
    padding: 16px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 0;
    padding-bottom: 0;
}

@media (max-width: 768px) {
    .card {
        margin: 5px;
        width: calc(100% - 10px);
    }
}



