.rankings ul {
    list-style-type: none;
    margin: 0;
    padding: 13px 16px;
}

.rankings ul li {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 15px;
}

.rankings ul li img {
    max-height: 70px;
    max-width: 70px;
    border: 3px solid #e1e1c4;
    border-radius: 100%;
    margin: 5px;
}

.rankings ul li .player-name {
    text-align: left;
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 3rem;
    padding: 5px 15px;
    -ms-flex-item-align: center;
        align-self: center;
    white-space: nowrap;
}

.rankings ul li .ranking-badge {
    border: .5px solid #e1e1c4;
    background: 0 0;
    border-radius: 5px;
    color: #e1e1c4;
    font-size: 2.2rem;
    -ms-flex-item-align: center;
        align-self: center;
    padding: 3px 8px;
    min-width: 70px;
    text-align: center;
}



