.top-nav {
    height: 60px;
    height: calc(60px + env(safe-area-inset-top));
    padding: 5px 8.33333%;
    background: #1d1d1d;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding-top: calc(5px + env(safe-area-inset-top));
}

.top-nav .logo {
    max-width: 250px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    margin: 0 auto;
}

.top-nav .navbar {
    display: -ms-flexbox;
    display: flex;
    height: auto;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-pack: end;
        justify-content: flex-end;
    padding-bottom: 0;
}

.top-nav .navbar .nav-link {
    -ms-flex: initial;
        flex: initial;
}

.top-nav .logo img {
    height: 100%;
}

@media (max-width: 768px) {
    .top-nav .navbar {
        display: none;
    }

    .top-nav .logo {
        margin: 0 auto;
    }
}



